import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, FormArray, Validators } from '@angular/forms';
import { ApiService } from './../../../app/core/_services/api.service';
import Swal from 'sweetalert2';
import { MemberConfigModel } from './MemberConfigModel';

@Component({
  selector: 'app-membercontrest',
  templateUrl: './membercontrest.component.html',
  styleUrls: ['./membercontrest.component.scss']
})

export class MembercontrestComponent implements OnInit {
  MemberContRestForm: FormGroup;
  memberConfigModel: MemberConfigModel = new MemberConfigModel();
  individualList: any[];
  proffessionSettings: {};
  IndividualList: any;
  InstitutionList: any[];
  MemberShipId: any;

  constructor(private apiService: ApiService, private fb: FormBuilder) {
    this.MemberContRestForm = this.fb.group({
      individualInstitute: ['', Validators.required],
      news: ['', Validators.compose([Validators.required, Validators.pattern('[0-9 ]*'), Validators.maxLength(3), Validators.minLength(1)])],
      gallery: ['', Validators.compose([Validators.required, Validators.pattern('[0-9 ]*'), Validators.maxLength(3), Validators.minLength(1)])],
      artimages: ['', Validators.compose([Validators.required, Validators.pattern('[0-9 ]*'), Validators.maxLength(3), Validators.minLength(1)])],
      artpdf: ['', Validators.compose([Validators.required, Validators.pattern('[0-9 ]*'), Validators.maxLength(3), Validators.minLength(1)])],
      videos: ['', Validators.compose([Validators.required, Validators.pattern('[0-9 ]*'), Validators.maxLength(3), Validators.minLength(1)])],
      MemberName: ['', Validators.required],
      enableMovieAccess:['']
    });
  }

  ngOnInit() {
    this.proffessionSettings = {
      text: 'Select Member',
      singleSelection: true,
      enableSearchFilter: true,
      classes: "myclass custom-class",
      searchPlaceholderText: 'Search ' + this.memberConfigModel.memberProfession,
      badgeShowLimit: 5,
      groupBy: "category"
    };
  }


  GetOtherMemberList(evt: any) {
    this.IndividualList = [];
    let memberName = evt.target.value;
    let membership_id = evt.target.value;
    let name = {
      "firstName": memberName
    }
    membership_id = {
      "membership_id": membership_id
    }
    if (memberName.length >= 2) {
      this.apiService.Create("member/getMemberDetailsByMemberName", name).subscribe(data => {
        if (data.success == true) {
          if (data != null && data != undefined) {
            if (data.success == true) {
              if (data.object != undefined) {
                let membershipDtoList = data.object;
                let IndvidualList = [];
                let InstituteList = [];
                membershipDtoList.forEach(memname => {
                  if (memname.membershipId > 0) {
                    IndvidualList.push({ "id": memname.membershipId, "itemName": memname.firstName, "profileImage": memname.profileImage });
                  }
                });
                this.IndividualList = IndvidualList;
                this.InstitutionList = InstituteList;
              }
            }
          }
        }
      }, error => {
        console.log(error);
      });
    }
  }


  onproffessionSelect(item: any) {
    console.log(item);
    let data: any;
    this.memberConfigModel.membershipId = item.id
    data = {
      "membershipId": item.id
    }
    this.apiService.Create("masterContentRestrictConfig/getByMemberId", data).subscribe((data1) => {
      if (data1.success == true) {
        this.memberConfigModel = data1.object;
        this.memberConfigModel.membershipId = data1.object.membershipDto.membershipId;
      } else if (data1.success == false) {
        // this.memberConfigModel.membershipId = ;
      }
    });
  }

  submitContent() {
    let data = {
      "membershipId": this.memberConfigModel.membershipId,
      "newsLimit": this.memberConfigModel.newsLimit,
      "galaryLimit": this.memberConfigModel.galaryLimit,
      "articleImageLimit": this.memberConfigModel.articleImageLimit,
      "articlePdfLimit": this.memberConfigModel.articlePdfLimit,
      "videoLimit": this.memberConfigModel.videoLimit,
      "enableMovieAccess":this.memberConfigModel.enableMovieAccess
    }
    this.apiService.Create("masterContentRestrictConfig/save", data).subscribe(response => {
      if (response.success == true) {
        Swal.fire(response.message);
      }
    });
    // this.ngOnInit();
    // this.MemberContRestForm.reset();
  }
  clearContent() {
    this.MemberContRestForm.reset();
  }


}

