import { Component, OnInit } from '@angular/core';
import { ApiService } from './../../../app/core/_services/api.service';
import { FormBuilder, FormControl, Validators, FormGroup } from '@angular/forms';
import { LoaderService } from './../../../app/core/_services/loader.service';
import { AuthService } from './../../auth/auth.service';
import { Login } from './../../account/login/login';
import { AccountService } from 'src/app/services/account.service';
import { Router, ActivatedRoute } from '@angular/router';
import Swal from 'sweetalert2';

@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.scss']
})
export class LoginComponent implements OnInit {

  loginForm: FormGroup;
  Mobile = new FormControl();
  Password = new FormControl();
  returnUrl: string;
  errorMessage: string;
  invalidLogin: boolean;

  constructor(private apiService: ApiService, private fb: FormBuilder, private loaderService: LoaderService, private authService: AuthService, private acc: AccountService, private router: Router, private route: ActivatedRoute) { }
  login: Login = new Login();

  ngOnInit() {
    //   this.login = new Login();
    if (localStorage.getItem('staffLogin')) {
      localStorage.clear();
      location.reload();
    }
    this.Mobile = new FormControl('', [Validators.required]);
    this.Password = new FormControl('', [Validators.required]);
    this.returnUrl = this.route.snapshot.queryParams['returnUrl'] || '/';

    this.loginForm = this.fb.group({
      "Mobile": this.Mobile,
      "Password": this.Password
    });
  }


  OnSubmit() {
    debugger;
    if (this.loginForm.valid) {
      let userLogin = this.loginForm.value;
      this.acc.Login(userLogin.Mobile, userLogin.Password).subscribe(result => {
        //this.router.navigate(['home']);
        //this.invalidLogin = false;
        if (result.success == true) {
          let data = result.object;
          if (data.roleDto != undefined && data.roleDto != null) {
            this.router.navigate(['home']);
          }
          else {
            Swal.fire("Role Not Found, Please Contact Super Admin");
          }

        }
        else {
          Swal.fire(result.message);
        }
      },
        error => {
          this.invalidLogin = true;
          this.errorMessage = "Invalid Usre Details!"
        })
    } else {
      this.apiService.validateAllFormFields(this.loginForm);
      //swal.fire("Please enter all the fields");
    }
  }

  isLogin() {
    this.authService.login(this.login);
  }
}
