import { Component, OnDestroy, OnInit } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Subject } from 'rxjs';
import { map } from 'rxjs/operators';
import { createmovie } from '../create-movie/create-movie';
import { ApiService } from 'src/app/core/_services/api.service';

@Component({
  selector: 'app-dummy-table',
  templateUrl: './dummy-table.component.html',
  styleUrls: ['./dummy-table.component.scss']
})
export class DummyTableComponent implements OnDestroy, OnInit {

  dtOptions: DataTables.Settings = {};
  persons: createmovie[] = [];
  dtTrigger: Subject<any> = new Subject<any>();

  constructor(private apiservice: ApiService) { }

  ngOnInit(): void {
    this.dtOptions = {
      pagingType: 'full_numbers',
      pageLength: 2
    };
    this.apiservice.getDATA("movieDetails/getAllActive").subscribe(data => {
        this.persons = (data as any).object;
        this.dtTrigger.next();
      });
  }

  ngOnDestroy(): void {
    this.dtTrigger.unsubscribe();
  }
}