import { Component, OnInit, ViewChild, ElementRef } from '@angular/core';
import { UserReportModal } from './../userreport/UserReportModal';
import { ApiService } from '../../core/_services/api.service';
import { FormBuilder, FormGroup, FormArray, Validators } from '@angular/forms';
import { BsDatepickerConfig } from 'ngx-bootstrap/datepicker';
import { LoaderService } from 'src/app/core/_services/loader.service';
import { DatePipe } from '@angular/common';
import * as XLSX from 'xlsx';
import { saveAs } from 'file-saver';
import swal from 'sweetalert2';
import { setDate } from 'ngx-bootstrap/chronos/utils/date-setters';
import { getDate } from 'ngx-bootstrap/chronos/utils/date-getters';

@Component({
  selector: 'app-usernotregistration',
  templateUrl: './usernotregistration.component.html',
  styleUrls: ['./usernotregistration.component.scss']
})
export class UsernotregistrationComponent implements OnInit {
  @ViewChild('TABLE', { static: false }) TABLE: ElementRef;
  title = 'Excel';

  datePickerConfig: Partial<BsDatepickerConfig>;
  UserReport: UserReportModal = new UserReportModal();
  UserReportForm: FormGroup;

  startDate = new Date();
  endDate: any; //= new Date();
  //maxstartDate = new Date();
  //maxendDate = new Date();
  minendDate: any;
  maxstartDate: any;
  minstartDate: any;

  Details: any[];

  constructor(private apiService: ApiService, private fb: FormBuilder, private loaderService: LoaderService, private datePipe: DatePipe) {
    this.UserReportForm = this.fb.group({
      startDate: ['', Validators.required],
      endDate: ['', Validators.required]
    });
    this.datePickerConfig = Object.assign({}, {
      containerClass: 'theme-dark-blue',
      showWeekNumbers: false,
      dateInputFormat: 'DD-MM-YYYY'
    });
  }

  ngOnInit() { }

  OndateChange(sdate) {
    if (sdate != null) {
      let startDate = new Date(this.UserReport.startDate);
      this.minendDate = new Date(this.startDate.setDate(startDate.getDate()));
      if (this.UserReport.startDate == undefined) {
        let EndDate = new Date(this.UserReport.endDate);
        this.maxstartDate = new Date(EndDate.setDate(EndDate.getDate()));
      }
    }
  }

  OnSubmit() {
    if (this.UserReportForm.valid) {
      let data = this.UserReport;
      var sd = new Date(this.UserReport.startDate);
      var ed = new Date(this.UserReport.endDate);
      this.Details = [];
      //this.loaderService.display(true);
      let jsonData = {
        "startDate": sd.getFullYear() + '-' + (sd.getMonth() + 1) + '-' + sd.getDate(),
        "endDate": ed.getFullYear() + '-' + (ed.getMonth() + 1) + '-' + ed.getDate()
      }
      this.apiService.Create("user/getUserOnDate", jsonData).subscribe(Response => {
          this.loaderService.display(false);
          if (Response.object != undefined) {
            this.Details = Response.object;
            if (this.Details.length == 0) {
              swal.fire("Records Not Available!");
            }
          }
          else {
            swal.fire(Response.message);
          }
        },
          (err) => {
            if (err.status === 400) {
              // handle validation error
              let validationErrorDictionary = JSON.parse(err.text());
              for (var fieldName in validationErrorDictionary) {
                if (validationErrorDictionary.hasOwnProperty(fieldName)) {
                  if (this.UserReportForm.controls[fieldName]) {
                    // integrate into angular's validation if we have field validation
                    this.UserReportForm.controls[fieldName].setErrors({ invalid: true });
                  } else {
                    // if we have cross field validation then show the validation error at the top of the screen
                    swal.fire(validationErrorDictionary[fieldName]);
                  }
                }
              }
            } else {
              swal.fire("something went wrong!");
            }
          });
    }
    else {
      this.apiService.validateAllFormFields(this.UserReportForm);
    }
  }

  Clear() {
    this.UserReportForm.reset();
    this.Details = [];
  }

  ExportToExcel() {
    const ws: XLSX.WorkSheet = XLSX.utils.table_to_sheet(this.TABLE.nativeElement);
    const wb: XLSX.WorkBook = XLSX.utils.book_new();
    XLSX.utils.book_append_sheet(wb, ws, 'Sheet1');
    XLSX.writeFile(wb, 'UserReport.xlsx');
    saveAs(new Blob(XLSX.write(wb), { type: "application/octet-stream" }), "test.xlsx");
  }

  printComponent(printSectionId: string) {
    const printContents = document.getElementById(printSectionId).innerHTML;
    const stylesHtml = this.getTagsHtml('style');
    const linksHtml = this.getTagsHtml('link');
    const popupWin = window.open('', '_blank', 'top=0,left=0,height=100%,width=auto');
    popupWin.document.open();
    popupWin.document.write(`
    <html>
        <head>
            ${linksHtml}
            ${stylesHtml}
        </head>
        <body onload="window.print(); window.close()">
            ${printContents}
        </body>
    </html>
    `
    );
    popupWin.document.close();
  }

  private getTagsHtml(tagName: keyof HTMLElementTagNameMap): string {
    const htmlStr: string[] = [];
    const elements = document.getElementsByTagName(tagName);
    for (let idx = 0; idx < elements.length; idx++) {
      htmlStr.push(elements[idx].outerHTML);
    }
    return htmlStr.join('\r\n');
  }
}
