import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { ApiService } from './../../../app/core/_services/api.service';
import { NgxImageCompressService } from 'ngx-image-compress';
import { Subject } from 'rxjs';
import Swal from 'sweetalert2';
import { LoaderService } from 'src/app/core/_services/loader.service';
import { DatePipe } from '@angular/common';
import {Comment} from './comment';

@Component({
  selector: 'app-comment-delete',
  templateUrl: './comment-delete.component.html',
  styleUrls: ['./comment-delete.component.scss']
})
export class CommentDeleteComponent implements OnInit {

  dtTrigger: Subject<any> = new Subject();
  commentForm:FormGroup;
  comment:Comment = new Comment();

  constructor(private apiService: ApiService, private fb: FormBuilder, private imageCompress: NgxImageCompressService, private loaderService: LoaderService, private datePipe: DatePipe) {
    this.commentForm = this.fb.group({
      comment:['']
    })
  }

  ngOnInit() {
  }

  searchResult:any;
  search(){
    let comm = {
      "comment": this.comment.comment
    }
    this.loaderService.display(true);
    this.apiService.Create("likeComment/findByComment",comm).subscribe(data=>{
      this.loaderService.display(false);
      if(data.success == true){
        if(data.object != null && data.object != undefined){
          this.searchResult = data.object;
        }
        this.dtTrigger.next();
      }
      else{
        Swal.fire(data.message);
      }
    })
  }

  deleteComment(commid){
    let comid={
      "likecommentId": commid
    }
    this.loaderService.display(true);
    this.apiService.Create("likeComment/deletByLikeCommentId", comid).subscribe(data=>{
      this.loaderService.display(false);
      if(data.success == true){
        if(data.object != null && data.object != undefined){
          Swal.fire(data.message);
        
        }
      }
    })
  }

  clearComments(){
    this.comment.comment = null;
  }
}
