import { Component, ChangeDetectorRef } from '@angular/core';
import { NgxSpinnerService } from "ngx-spinner";
import { LoaderService } from './core/_services/loader.service';
import { PlatformLocation } from '@angular/common';
import { ApiService } from './core/_services/api.service';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css']
})
export class AppComponent {
  title = 'sinighama';
  showLoader: boolean;
  backDisable: any;
  constructor(private loaderService: LoaderService, private apiService:ApiService, private spinner: NgxSpinnerService, public cdRef: ChangeDetectorRef, location: PlatformLocation) {

    this.backDisable = location;
    location.onPopState(() => {
      // var r = confirm("You pressed a Back button! Are you sure?!");

      // if (r == true) {
      //     // Call Back button programmatically as per user confirmation.
      //     history.back();
      //     // Uncomment below line to redirect to the previous page instead.
      //     // window.location = document.referrer // Note: IE11 is not supporting this.
      // } else {
      //     // Stay on the current page.
      //     history.pushState(null, null, window.location.pathname);
      // }
      history.pushState(null, null, window.location.pathname);
    });
  }

  ngOnInit() {

    this.backDisable.onPopState(() => {
      // var r = confirm("You pressed a Back button! Are you sure?!");
      // if (r == true) {
      //     // Call Back button programmatically as per user confirmation.
      //     history.back();
      //     // Uncomment below line to redirect to the previous page instead.
      //     // window.location = document.referrer // Note: IE11 is not supporting this.
      // } else {
      //     // Stay on the current page.
      //     history.pushState(null, null, window.location.pathname);
      // }

      history.pushState(null, null, window.location.pathname);
    });
    /** spinner starts on init */
    this.spinner.show();

    setTimeout(() => {
      /** spinner ends after 5 seconds */
      this.spinner.hide();
    }, 5000);

    this.showLoader = true;
    this.loaderService.status.subscribe((val: boolean) => {
      this.showLoader = val;
    });
  }

  ngAfterViewInit() {
    this.showLoader = true;
    setTimeout(() => {
      this.loaderService.status.subscribe((val: boolean) => {
        this.showLoader = val;
      });
    });
    this.cdRef.detectChanges();
  }

  update() {
    // Run change detection only for this component when update() method is called.
    this.cdRef.detectChanges();
  }

}