  imageCroppedProfile(event: ImageCroppedEvent, seqno: number) {
    var orientation = -1;
    var imgResultBeforeCompress: string;
    imgResultBeforeCompress = event.base64;
    let isbc = this.imageCompress.byteCount(imgResultBeforeCompress);
    if (isbc < 200000) {
      this.imageCompress.compressFile(event.base64, orientation, 90, 88).then(
        result => {
          if (this.imageChangedEvent1Profile) {
            this.url1 = result;
          }
          else if (this.imageChangedEvent2Profile) {
            this.url2 = result;
          }
          else if (this.imageChangedEvent3Profile) {
            this.url3 = result;
          }
          else if (this.imageChangedEvent4Profile) {
            this.url4 = result;
          }
          else if (this.imageChangedEvent5Profile) {
            this.url5 = result;
          }
          else if (this.imageChangedEvent6Profile) {
            this.url6 = result;
          }
          else if (this.imageChangedEvent7Profile) {
            this.url7 = result;
          }
          this.loaderService.display(false);
          const csv: string = result as string;
          let cur = [];
          cur = this.imageSendArr.filter((temp: any) => temp.seqNum == seqno);
          if (cur.length > 0) {
            this.imageSendArr.forEach(da => {
              if (da.seqNum == seqno) {
                da.imagePath = csv;
              }
            });
          } else {
            let obj = {
              "imagePath": csv,
              "seqNum": seqno
            };
            this.imageSendArr.push(obj);
          }
        })
    }
    else if (isbc > 200000 && isbc < 5000000) {
      this.imageCompress.compressFile(event.base64, orientation, 40, 40).then(
        result => {
          if (this.imageChangedEvent1Profile) {
            this.url1 = result;
          }
          else if (this.imageChangedEvent2Profile) {
            this.url2 = result;
          }
          else if (this.imageChangedEvent3Profile) {
            this.url3 = result;
          }
          else if (this.imageChangedEvent4Profile) {
            this.url4 = result;
          }
          else if (this.imageChangedEvent5Profile) {
            this.url5 = result;
          }
          else if (this.imageChangedEvent6Profile) {
            this.url6 = result;
          }
          else if (this.imageChangedEvent7Profile) {
            this.url7 = result;
          }
          this.loaderService.display(false);
          const csv: string = result as string;
          let cur = [];
          cur = this.imageSendArr.filter((temp: any) => temp.seqNum == seqno);
          if (cur.length > 0) {
            this.imageSendArr.forEach(da => {
              if (da.seqNum == seqno) {
                da.imagePath = csv;
              }
            });
          } else {
            let obj = {
              "imagePath": csv,
              "seqNum": seqno
            };
            this.imageSendArr.push(obj);
          }
        })
    }
    else if (isbc > 500000 && isbc < 10000000) {
      this.imageCompress.compressFile(event.base64, orientation, 35, 35).then(
        result => {
          if (this.imageChangedEvent1Profile) {
            this.url1 = result;
          }
          else if (this.imageChangedEvent2Profile) {
            this.url2 = result;
          }
          else if (this.imageChangedEvent3Profile) {
            this.url3 = result;
          }
          else if (this.imageChangedEvent4Profile) {
            this.url4 = result;
          }
          else if (this.imageChangedEvent5Profile) {
            this.url5 = result;
          }
          else if (this.imageChangedEvent6Profile) {
            this.url6 = result;
          }
          else if (this.imageChangedEvent7Profile) {
            this.url7 = result;
          }
          this.loaderService.display(false);
          const csv: string = result as string;
          let cur = [];
          cur = this.imageSendArr.filter((temp: any) => temp.seqNum == seqno);
          if (cur.length > 0) {
            this.imageSendArr.forEach(da => {
              if (da.seqNum == seqno) {
                da.imagePath = csv;
              }
            });
          } else {
            let obj = {
              "imagePath": csv,
              "seqNum": seqno
            };
            this.imageSendArr.push(obj);
          }
        })
    }
  }
