import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, FormArray, Validators } from '@angular/forms';
import { ApiService } from '../../core/_services/api.service';
import Swal from 'sweetalert2';
import { from, Subject } from 'rxjs';
import { ContentConfiguration } from './contentconfiguration';

import { LoaderService } from 'src/app/core/_services/loader.service';
@Component({
  selector: 'app-contentrest',
  templateUrl: './contentrest.component.html',
  styleUrls: ['./contentrest.component.scss']
})
export class ContentrestComponent implements OnInit {
  ContentRestForm:FormGroup;
  contentConfig : ContentConfiguration = new ContentConfiguration();
 
  constructor(private apiService:ApiService,private fb:FormBuilder) { 
    this.ContentRestForm = this.fb.group({
      news_limit:['', Validators.compose([Validators.required, Validators.pattern('[0-9 ]*'), Validators.maxLength(3), Validators.minLength(1)])],
      galary_limit:['', Validators.compose([Validators.required, Validators.pattern('[0-9 ]*'), Validators.maxLength(3), Validators.minLength(1)])],
      article_image_limit:['', Validators.compose([Validators.required, Validators.pattern('[0-9 ]*'), Validators.maxLength(3), Validators.minLength(1)])],
      article_pdf_limit:['', Validators.compose([Validators.required, Validators.pattern('[0-9 ]*'), Validators.maxLength(3), Validators.minLength(1)])],
      video_limit:['', Validators.compose([Validators.required, Validators.pattern('[0-9 ]*'), Validators.maxLength(3), Validators.minLength(1)])]
    });
  }

  ngOnInit() {
    this.getOnloadData();
  }

 configureData:any[];
  getOnloadData() {
    debugger;  
    this.apiService.getDATA("masterConfiguration/getAll").subscribe(data => {
      if (data.object != null && data.object != undefined) {
        if(data.object.masterConfigurationDtoList.length > 0){
          this.contentConfig = data.object.masterConfigurationDtoList[0];
        }               
      }
    });
  }

  onSubmit(){   
   this.apiService.Create("masterConfiguration/save",this.contentConfig).subscribe( response =>{
      if(response.success ==  true){
        this.getOnloadData();
        Swal.fire(response.message);
        this.getOnloadData();
      }
    });
  }
}

