import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, FormArray, Validators } from '@angular/forms';
import { ApiService } from './../../../app/core/_services/api.service';
import { LoaderService } from 'src/app/core/_services/loader.service';
import { DataTableDirective } from 'angular-datatables';
import { NgxImageCompressService } from 'ngx-image-compress';
import { ImageCroppedEvent } from 'ngx-image-cropper';
import { Subject } from 'rxjs';

import Swal from 'sweetalert2';
import { MovieConfigModel } from './MovieConfigModel';


@Component({
  selector: 'app-moviecontrest',
  templateUrl: './moviecontrest.component.html',
  styleUrls: ['./moviecontrest.component.scss']
})
export class MoviecontrestComponent implements OnInit {
  MovieContRestForm: FormGroup;
  movieConfigModel: MovieConfigModel= new MovieConfigModel();
  movieList: any[];
  MovieId: any;
  proffessionSettings: {};
  selectedItems = [];
  selectedMovie:any[];
  

  constructor(private apiService: ApiService, private fb: FormBuilder,private loaderService: LoaderService) { 

    this.MovieContRestForm = this.fb.group({
      news: ['', Validators.compose([Validators.required, Validators.pattern('[0-9 ]*'), Validators.maxLength(3), Validators.minLength(1)])],
      gallery: ['', Validators.compose([Validators.required, Validators.pattern('[0-9 ]*'), Validators.maxLength(3), Validators.minLength(1)])],
      artimages: ['', Validators.compose([Validators.required, Validators.pattern('[0-9 ]*'), Validators.maxLength(3), Validators.minLength(1)])],
      artpdf: ['', Validators.compose([Validators.required, Validators.pattern('[0-9 ]*'), Validators.maxLength(3), Validators.minLength(1)])],
      videos: ['', Validators.compose([Validators.required, Validators.pattern('[0-9 ]*'), Validators.maxLength(3), Validators.minLength(1)])],
      MovieName: ['', Validators.required]
    });
  }

  ngOnInit() {
    this.proffessionSettings = {
      text: 'Select',
      singleSelection: true,
      enableSearchFilter: true,
      classes: "myclass custom-class",
      searchPlaceholderText: 'Search ',
      badgeShowLimit: 5,
      groupBy: "category"
    };
    this.submitContent();
  
  }

  
  getMovieList(event: any) {
    let movieName = event.target.value;
    let movieNameSearch = {
      "title": movieName
    }
    this.loaderService.display(true);
    this.apiService.Create("movieDetails/searchmovie", movieNameSearch).subscribe(data => {
      this.loaderService.display(false);
      let memList = [];
      let memdata = data.object;
      if (memdata != null && memdata != undefined) {
        memdata.forEach(ml => {
          memList.push({ "id": ml.movieId, "itemName": ml.title, "profileImage": ml.movieImagesDtoList[0].poster })
        })
        this.movieList = memList;
      }
    })
  }

  selectedMoviesId: any;
  selectedMovieId(movdata: any) {
    this.selectedMoviesId = movdata.id;
  }

  onproffessionSelect(item: any) {
    console.log(item);
    let data: any;
    this.movieConfigModel.movieId = item.id
    data = {
      "movieId": item.id
    }
    this.apiService.Create("moviecontentresriction/getByMovieId", data).subscribe((data1) => {
      if (data1.success == true) {
        this.movieConfigModel = data1.object;
        this.movieConfigModel.movieId = data1.object.movieDetailsDto.movieId;
      } else if (data1.success == false) {
        // this.memberConfigModel.membershipId = ;
      }
    });
  }


  
  submitContent() {
  debugger;
    let data = {
      "movieId": this.movieConfigModel.movieId,
      "newsLimit": this.movieConfigModel.newsLimit,
      "galaryLimit": this.movieConfigModel.galaryLimit,
      "articleImageLimit": this.movieConfigModel.articleImageLimit,
      "articlePdfLimit": this.movieConfigModel.articlePdfLimit,
      "videoLimit": this.movieConfigModel.videoLimit,
  
    }
    this.apiService.Create("moviecontentresriction/save", data).subscribe(response => {
      if (response.success == true) {
        Swal.fire(response.message);
      }
    });
    //  this.ngOnInit();
    //  this.MovieContRestForm.reset();
    
    
  }

  clearContent() {
    this.MovieContRestForm.reset();
    this.ngOnInit();
  }


  }



