import { Component, OnInit } from '@angular/core';
import { Subject } from 'rxjs';
import { ApiService } from 'src/app/core/_services/api.service';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';
import { HeaderMessage } from './header-message';
import Swal from 'sweetalert2';
import { LoaderService } from './../../core/_services/loader.service';


@Component({
  selector: 'app-header-message',
  templateUrl: './header-message.component.html',
  styleUrls: ['./header-message.component.scss']
})
export class HeaderMessageComponent implements OnInit {

  headerMessageForm: FormGroup;
  headerMessage: HeaderMessage = new HeaderMessage();
  dtOptions: DataTables.Settings = {};
  dtTrigger: Subject<any> = new Subject();

  constructor(private apiService: ApiService, private fb: FormBuilder, private loaderService: LoaderService) {
    this.headerMessageForm = this.fb.group({
      kannadaMsg: ['', Validators.required],
      engMsg: ['', Validators.required]
    })
  }

  ngOnInit() { }

  onSubmit() {
    let messageData = {
      "marqueeId": this.headerMessage.marqueeId,
      "kannadaMsg": this.headerMessage.kannadaMsg,
      "engMsg": this.headerMessage.engMsg
    }
    this.loaderService.display(true);
    this.apiService.Create("headerMarquee/save", messageData).subscribe(data => {
      this.loaderService.display(false);
      if (data.success == true) {
        if (data.object != null && data.object != undefined) {
          Swal.fire(data.message);
          this.clearMessage();
        }
      }
      else {
        this.loaderService.display(false);
        Swal.fire(data.message);
      }
    })
  }

  clearMessage() {
    this.headerMessageForm.reset();
  }

}
