import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { BehaviorSubject, Observable } from 'rxjs';
import { map } from 'rxjs/operators';
import { Router } from '@angular/router';

@Injectable({
  providedIn: 'root'
})
export class AccountService {

  private currentUserSubject: BehaviorSubject<any>;
  public currentUser: Observable<any>;
  private UsreRoleName: BehaviorSubject<any>;
  private UserRoleId: BehaviorSubject<any>;

 //private baseUrllogin : string = "http://localhost:6060/staff/login";
  private baseUrllogin: string = "https://sinilinux.azurewebsites.net/staff/login";
  //private baseUrllogin : string = "http://43.247.156.175:8080/sinighama/staff/login";

  constructor(private Http: HttpClient, private router: Router) {
    this.currentUserSubject = new BehaviorSubject<any>(JSON.parse(localStorage.getItem('staffLogin')));
    this.currentUser = this.currentUserSubject.asObservable();
  }

  public get currentUserValue() {
    return this.currentUserSubject.value;
  }

  private loginStatus = new BehaviorSubject<boolean>(this.chechLoginStatus());

  Login(mobile: string, password: string) {
    localStorage.clear();
    return this.Http.post<any>(this.baseUrllogin, { mobile, password }).pipe(
      map(object => {
        if (object != null) {
          localStorage.setItem('loginStatus', '1');
          localStorage.setItem("staffLogin", JSON.stringify(object));
          //this.loginStatus.next(true);
          //this.router.navigate(['/home']);
          this.currentUserSubject.next(object);
          return object;
        } else {
          this.loginStatus.next(false);
          this.router.navigate(['/']);
        }
        //return object;
      })
    );
  }

  LogOut() {
    localStorage.removeItem('staffLogin');
    this.currentUserSubject.next(null);
  }

  get isLoggesIn() {
    return this.loginStatus.asObservable();
  }

  get currentRoleId() {
    return this.UserRoleId.asObservable();
  }

  chechLoginStatus(): boolean {
    return false;
  }

}
