import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { Observable } from 'rxjs';
import { AuthService } from './../../auth/auth.service';
import { Permission } from './../../modals/permission';
import { AccountService } from 'src/app/services/account.service';
import { ApiService } from '../_services/api.service';

@Component({
  selector: 'core-header',
  templateUrl: './header.component.html',
  styleUrls: ['./header.component.css']
})
export class HeaderComponent implements OnInit {

  clicked: boolean;
  isLoggedIn$: Observable<boolean>;

  permission: Permission = new Permission();
  menuHide: boolean = true;
  superadminmenu: boolean = true;

  menueValue: any;
  UserName: any;
  staffMenu: boolean = true;

  currentUser: any;
  //show: boolean;
  //rId : any;
  constructor(private router: Router, private authService: AuthService, private acc: AccountService, private apiService: ApiService) {
    //this.clicked = this.clicked === undefined ? false : true;
    this.acc.currentUser.subscribe(x => this.currentUser = x);
  }
  LoginStatus$: Observable<boolean>;
  UsreRoleName$: Observable<string>;
  UserRoleId$: Observable<string>;
  currentUserValue$: Observable<any>;

  ngOnInit() {
    this.getCount();
  }

  onLogout() {
    this.acc.LogOut();
    this.router.navigate(['/login']);
  }
  listq: any[] = [];
  getCount() {
    let count = 0;
    this.apiService.getDATA("helpDesk/getAll").subscribe((response) => {
      if (response.success == true) {
        if (response.object != null && response.object != undefined) {
          this.listq = response.object.helpDeskDtoList;
        }
      }
    })
  }
}