import { NgModule } from '@angular/core';
import { BrowserModule } from '@angular/platform-browser';
import { HttpClientModule } from '@angular/common/http';
import { HttpModule } from '@angular/http';
import { CommonModule } from '@angular/common';
import { CreateMovieComponent } from './create-movie/create-movie.component';
import { DataTablesModule } from 'angular-datatables';
import { QuillModule } from 'ngx-quill';
import { AngularMultiSelectModule } from 'angular2-multiselect-dropdown';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { SanitizeHtmlPipe } from '../sanitize-html.pipe';
import { NgxImageCompressService } from 'ngx-image-compress';
import { ImageCropperModule } from 'ngx-image-cropper';
import { BsDatepickerModule, BsDatepickerConfig } from 'ngx-bootstrap/datepicker';

import { MastergenersComponent } from './mastergeners/mastergeners.component';
import { MovielistComponent } from './movielist/movielist.component';
import { DummyTableComponent } from './dummy-table/dummy-table.component';
import { MovienewsComponent } from './movienews/movienews.component';
import { MembermoviemappingComponent } from './membermoviemapping/membermoviemapping.component';



export function getDatepickerConfig(): BsDatepickerConfig {
  return Object.assign(new BsDatepickerConfig(), {
    dateInputFormat: 'DD-MM-YYYY'
  });
}

@NgModule({
  declarations: [
    CreateMovieComponent,
    MastergenersComponent, MovielistComponent, DummyTableComponent, MovienewsComponent, MembermoviemappingComponent
  ],
  imports: [
    BrowserModule,
    HttpClientModule,
    HttpModule,
    CommonModule,
    DataTablesModule,
    AngularMultiSelectModule,
    ImageCropperModule,
    BsDatepickerModule.forRoot(),
    QuillModule.forRoot(),
    FormsModule,
    ReactiveFormsModule
  ],

  providers: [SanitizeHtmlPipe, NgxImageCompressService, { provide: BsDatepickerConfig, useFactory: getDatepickerConfig }]
})
export class MovieModule { }
