import { Component, Injectable, Inject } from '@angular/core';
import { Http, Response, RequestOptions, Headers } from '@angular/http';
import { Observable, of, Observer, BehaviorSubject } from 'rxjs';
//import { Observable } from 'rxjs/Observable';
import { map } from 'rxjs/operators';
import { HttpClient } from '@angular/common/http';
import { FormGroup, FormControl, FormArray } from '@angular/forms';

@Injectable({
	providedIn: 'root'
})
export class ApiService {

	headers: Headers;
	headers1: Headers;
	options: RequestOptions;
	options1: RequestOptions;
	_baseUrl: string = "https://sinilinux.azurewebsites.net/";
	// _baseUrl: string = "http://localhost:6060/";
	//_baseUrl: string = "http://43.247.156.175:8080/sinighama/";

	constructor(private httpClient: HttpClient, private http: Http) {

		this.headers = new Headers({
			'Content-Type': 'application/json',
			'Accept': 'application/json',
			'Access-Control-Allow-Origin': '*'
		});
		this.options = new RequestOptions({ headers: this.headers });
		//this._baseUrl = "http://localhost:8080/";
		this.headers1 = new Headers({
			'Content-Type': undefined,
			//'Access-Control-Allow-Origin': '*'
		});


		this.options1 = new RequestOptions({
			withCredentials: true,
			headers: this.headers1
		});

		//this._baseUrl = baseUrl;
	}


	//Api service for get method by Id
	getURI(Module: string, id: any) {
		return this.http.get(this._baseUrl + Module + "/" + id)
			.pipe(map((response: any) => response.json()
				.catch(this.handleError)));
	}

	getWithMultipleQueryTerm(Module: string, query: string) {
		return this.http
			.get(this._baseUrl + Module + "/" + query)
			.pipe(map((response: any) => response.json()
				.catch(this.handleError)
			));
	}

	//Api service for get method
	getDATA(Module: string) {
		return this.http.get(this._baseUrl + Module)
			.pipe(map((response: any) => response.json()
				//.catch (this.handleError)
			));
	}


	//Api service for post method
	Create(Module: string, user: any) {
		return this.http.post(this._baseUrl + Module, user, this.options)
			.pipe(map((response: any) => response.json()
				//.catch((error: any) => console.log(error))
			));
	}

	//Api Service for delete
	Delete(url: string, key: string, val: string) {
		return this.http.delete(this._baseUrl, this.options)
			.pipe(map((response: any) => response.json()
				.catch(this.handleError)));
	}

	Delete1(url: string, key: string, val: string) {
		return this.http
			.delete(url + "/?" + key + "=" + val, this.options).pipe(map(this.extractData))
		// .catch(this.handleError);
	}

	private extractData(res: Response) {
		let body = res.json();
		return body || {};
	}

	private handleError(error: any) {
		let errMsg = (error.message) ? error.message :
			error.status ? `${error.status} - ${error.statusText}` : 'Server error';
		console.error(errMsg);
		return Observable.throw(errMsg);
	}

	GetDatefromJson(date: any) {
		let onlydate: any = new Date();
		let sldate = new Date(date);
		return onlydate = sldate.getFullYear() + "-" + (((sldate.getMonth() + 1) < 10) ? ('0' + (sldate.getMonth() + 1)) : (sldate.getMonth() + 1)) + "-" + ((sldate.getDate() < 10) ? ('0' + sldate.getDate()) : sldate.getDate());
	}

	getURI2(Module: string, id: any, id1: boolean) {
		return this.http.get(this._baseUrl + Module + "/" + id + "/" + id1)
			.pipe(map((response: any) => response.json()
				.catch(this.handleError)));
	}


	getByMultipleParam(Module: string, id: any, id1: any) {
		return this.http.get(this._baseUrl + Module + "/" + id + "/" + id1)
			.pipe(map((response: any) => response.json()
				.catch(this.handleError)));
	}


	uploadImage(Module: string, formdata: any) {
		return this.http
			.post(this._baseUrl + Module, formdata).pipe(map(res => res.json().catch(this.handleError)));
	}


	CallURL(Module: string) {
		return this.http.get(Module).pipe(map(res => res.json()
			.catch(this.handleError)));
	}

	//Validate All Fields of Form
	validateAllFormFields(formGroup: FormGroup) {
		Object.keys(formGroup.controls).forEach(field => {
			const control = formGroup.get(field);
			if (control instanceof FormControl) {
				control.markAsTouched({ onlySelf: true });
			} else if (control instanceof FormGroup) {
				this.validateAllFormFields(control);
			} else if (control instanceof FormArray) {
				control.controls.forEach(control => {
					if (control instanceof FormControl) {
						control.markAsTouched({ onlySelf: true });
					} else if (control instanceof FormGroup) {
						this.validateAllFormFields(control);
					}
				})
			}
		});
	}

	getBase64ImageFromURL(url: string) {
		return Observable.create((observer: Observer<string>) => {
			// create an image object
			let img = new Image();
			img.crossOrigin = 'Anonymous';
			img.src = url;
			if (!img.complete) {
				// This will call another method that will create image from url
				img.onload = () => {
					observer.next(this.getBase64Image(img));
					observer.complete();
				};
				img.onerror = (err) => {
					observer.error(err);
				};
			} else {
				observer.next(this.getBase64Image(img));
				observer.complete();
			}
		});
	}

	getBase64Image(img: HTMLImageElement) {
		// We create a HTML canvas object that will create a 2d image
		var canvas = document.createElement("canvas");
		canvas.width = img.width;
		canvas.height = img.height;
		var ctx = canvas.getContext("2d");
		// This will draw image    
		ctx.drawImage(img, 0, 0);
		// Convert the drawn image to Data URL
		var dataURL = canvas.toDataURL("image/png");
		return dataURL.replace(/^data:image\/(png|jpg|jpeg|JPG|JPEG|PNG);base64,/, "");
	}

	async getBase64ImageFromUrl1(imageUrl) {
		var res = await fetch(imageUrl);
		var blob = await res.blob();
		return new Promise((resolve, reject) => {
			var reader = new FileReader();
			reader.addEventListener("load", function () {
				resolve(reader.result);
			}, false);

			reader.onerror = () => {
				return reject(this);
			};
			reader.readAsDataURL(blob);
		})
	}

	saveFormData(url: any, data: any) {
		return this.http.post(this._baseUrl + url, data).pipe(map((response: any) => response.json()));
	}

	public chatimmediate: BehaviorSubject<any> = new BehaviorSubject(null);
	setSubject(value) {
		if (value) {
			this.chatimmediate.next(value);
		} else {
			this.chatimmediate.next(null)
		}
	}
}
